/* COLORS
Platinum     #a8a8a8
Dim grey    #696773
Dark liver  #4C4A4B

*/

.calculator {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  border: 1px solid #000;
  width: 350px;
  max-width: 100%;
  border-radius: 7px;
}

.keypad {
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(4, 1fr);
  font-family: "Poppins", sans-serif;
}

.calcDisplay {
  height: 4rem;
  background-color: #383637;
  color: #fff;
  font-size: 2.5rem;
  display: flex;
  justify-content: flex-end;
  align-content: flex-end;
  font-family: "Poppins", sans-serif;
  border-radius: 4px;
}
.calcDisplay p {
  margin: 0 0.5rem;
}

.keypadButton {
  text-align: center;
  /* // border: '1px solid #fff', */
  border-radius: 7px;
  color: #fff;
  font-size: 1.5rem;
  background-color: #858585;
  height: 4rem;
}
.keypadButton:hover {
  background-color: #4c4a4b;
}
.operatorButton {
  background-color: #696773;
}
